import React, { useState, useEffect, useRef } from 'react';
import { NavLink } from 'react-router-dom';

import { adminRoot } from '../constants/defaultValues';
import { FormGroup } from 'reactstrap';
import classnames from 'classnames';
import GlideComponent from '../components/carousel/GlideComponent';
import { scroller } from 'react-scroll'
import Headroom from 'react-headroom'

const slideSettings = {
  type: "carousel",
  gap: 30,
  perView: 4,
  hideNav: true,
  peek: { before: 10, after: 10 },
  breakpoints: {
    "600": { perView: 1 },
    "992": { perView: 2 },
    "1200": { perView: 3 },
  },
};

const slideItems = [
  {
    icon: "iconsminds-mouse-3",
    title: "Contador de clicks",
    detail:
      "Sigue ada uno de tus enlaces haciendo una cuenta de cuantos clicks obtiene",
  },
  {
    icon: "iconsminds-electric-guitar",
    title: "Música",
    detail:
      "Puedes enviar videos de forma mas sencilla y rapida evitando la publicidad molesta",
  },
  {
    icon: "iconsminds-keyboard",
    title: "Enlaces",
    detail:
      "Es tan facil como copiar y pegar asi de fácil podrás acortar tus enlaces y enviarlos a tus conocidos",
  },
  {
    icon: "iconsminds-dvd",
    title: "Videos",
    detail:
      "Recomienda y envia a tus amigos la música que mas te gusta de una forma fácil,rápida y sencilla",
  },
];

const features = [
  {
    title: "Pleasant Design",
    img: "/assets/img/landing-page/features/plesant-design.png",
    detail:
      "As a web developer we enjoy to work on something looks nice. It is not an absolute necessity but it really motivates us that final product will look good for user point of view. <br /><br />So we put a lot of work into colors, icons, composition and design harmony. Themed components and layouts with same design language. <br /><br />We kept user experience principles always at the heart of the design process.",
  },
  {
    title: "Extra Responsive",
    img: "/assets/img/landing-page/features/extra-responsive.png",
    detail:
      "Xxs breakpoint is for smaller screens that has a resolution lower than 420px. Xs works between 576px and 420px. Xxl breakpoint is for larger screens that has a resolution higher than 1440px. Xl works between 1200px and 1440px.<br><br>With this approach we were able to create better experiences for smaller and larger screens.",
  },
  {
    title: "Superfine Charts",
    img: "/assets/img/landing-page/features/superfine-charts.png",
    detail:
      "Using charts is a good way to visualize data but they often look ugly and break the rhythm of design. <br /><br />We concentrated on a single chart library and tried to create charts that look good with color, opacity, border and shadow. <br /><br />Used certain plugins and created some to make charts even more useful and beautiful.",
  },
  {
    title: "Layouts for the Job",
    img: "/assets/img/landing-page/features/layouts-for-the-job.png",
    detail:
      "Layouts are the real thing, they need to be accurate and right for the job. They should be functional for both user and developer. <br /><br />We created lots of different layouts for different jobs.<br /><br />Listing pages with view mode changing capabilities, shift select and select all functionality, application layouts with an additional menu, authentication and error layouts which has a different design than the other pages were our main focus. We also created details page with tabs that can hold many components.",
  },
  {
    title: "Smart Menu",
    img: "/assets/img/landing-page/features/smart-menu.png",
    detail:
      "Instead of good old single panel menus with accordion structure that looks over complicated, we created 2 panels and categorized pages accordingly.<br><br>The default menu auto hides sub panel when resolution is under some breakpoint to open some space. You may also hide menu completely or use only main panel open only.",
  },
];




const Home = () => {
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const refRowHome = useRef(null);
  const refSectionHome = useRef(null);
  const refSectionFooter = useRef(null);
  const [links, setLinks] = useState([]);



  function fetchLinks() {
    fetch('http://localhost:3001/links')
      .then(res => res.json())
      .then(data => {
        setLinks(data)
      })
  }



  function addLink(e) {
    let valor_url = document.getElementById('linkurl')
    fetch('http://localhost:3001/links', {
      method: 'POST',
      body: JSON.stringify({ sourceLink: valor_url.value }),
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      }
    }).then(res => res.json())
      .then(data => console.log(data))
      .then(valor_url.value = "")
      .catch(err => console.error(err));
    e.preventDefault();
    fetchLinks();
  }




  useEffect(() => {
    fetchLinks();
    window.addEventListener('scroll', onWindowScroll);
    window.addEventListener('resize', onWindowResize);
    window.addEventListener('click', onWindowClick);

    document.body.classList.add('no-footer');
    return () => {
      window.removeEventListener('scroll', onWindowScroll);
      window.removeEventListener('resize', onWindowResize);
      window.removeEventListener('click', onWindowClick);
      document.body.classList.remove('no-footer');
    }
  }, []);

  const onWindowResize = (event) => {
    const homeRect = refRowHome.current.getBoundingClientRect()

    const homeSection = refSectionHome.current;
    homeSection.style.backgroundPositionX = homeRect.x - 580 + "px";

    const footerSection = refSectionFooter.current;
    footerSection.style.backgroundPositionX = event.target.innerWidth - homeRect.x - 2000 + "px";

    if (event.target.innerWidth >= 992) {
      setShowMobileMenu(false);
    }
  }

  const onWindowClick = () => {
    setShowMobileMenu(false);
  }

  const onWindowScroll = () => {
    setShowMobileMenu(false);
  }

  const scrollTo = (event, target) => {
    event.preventDefault();
    scroller.scrollTo(target, {
      duration: 500,
      delay: 0,
      smooth: "easeInOutQuart",
      offset: -100
    });
    return false;
  }


  return (
    <div className={classnames('landing-page', {
      'show-mobile-menu': showMobileMenu
    })}>
      <div className="mobile-menu" onClick={(event) => event.stopPropagation()} >
        <a className="logo-mobile c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'home')} >
          <span></span>
        </a>
        <ul className="navbar-nav">
        <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'link')}>LINK</a>                </li>
        <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'nosotros')}>¿QÚE HACEMOS?</a></li>
        <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'aliados')}>ALIADOS</a></li>
          <li className="nav-item">
            <div className="separator"></div>
          </li>
          <li className="nav-item"><a className="c-pointer" href="/user/login" >LOGIN </a></li>

        </ul>
      </div>

      <div className="main-container">
        <Headroom className="landing-page-nav">
          <nav>
            <div className="container d-flex align-items-center justify-content-between">
              <a className="navbar-logo pull-left c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'home')}>
                <span className="white"></span>
                <span className="dark"></span>
              </a>
              <ul className="navbar-nav d-none d-lg-flex flex-row">
              <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'link')}>LINK</a>                </li>
              <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'nosotros')}>¿QÚE HACEMOS?</a></li>
              <li className="nav-item"><a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'aliados')}>ALIADOS</a></li>

   
                <li className="nav-item pl-4">
                  <a className="btn btn-outline-semi-light btn-sm pr-4 pl-4" rel="noopener noreferrer"
                    href="/user/login">LOGIN</a>
                </li>
          
              </ul>
              <span className="mobile-menu-button" onClick={(event) => { setShowMobileMenu(!showMobileMenu); event.stopPropagation() }}>
                <i className="simple-icon-menu"></i>
              </span>
            </div>
          </nav>
        </Headroom>
        <div className="content-container" id="home">

          <div className="section home" ref={refSectionHome}>
            <div className="container">
              <div className="row home-row" ref={refRowHome}>
                <div className="col-12 d-block d-md-none">
                  <NavLink to="/">
                    <img alt="mobile hero" className="mobile-hero"
                      src="/assets/img/landing-page/prueba.png" />
                  </NavLink>
                </div>

                <div className="col-12 col-xl-4 col-lg-5 col-md-6">
                  <div className="home-text">
                    <div className="display-1">NO TE PREOCUPES <br />POR EL LIMITE</div>
                    <p className="white mb-5">
                      Las limitaciones de las paginas para poder enviar mensajes con cierta cantidad de caracteres.
                   
                    </p>
                    <NavLink className="btn btn-light btn-xl mr-2 mb-2" to='/user/register'>
                      REGISTARSE <i className="simple-icon-arrow-right"></i>
                    </NavLink>
                  </div>
                </div>
                <div className="col-12 col-xl-7 offset-xl-1 col-lg-7 col-md-6  d-none d-md-block">
                  <NavLink to={adminRoot}>
                    <img alt="hero" src="/assets/img/landing-page/prueba.png" />
                  </NavLink>
                </div>
              </div>

              <div className="container-fluid" id="link">
                <form className="my-4 form-inline" onSubmit={addLink} autoComplete="off">
                  <FormGroup className="col-12">
                    <input
                      placeholder="Acorta tu enlace"
                      type="url"
                      className="form-control col mr-2 buscar"
                      id="linkurl"
                      required

                    />
                    <button                      
                      className="btn btn-light btn-lg "
                      type="submit"
                    >
                      ACORTAR
                    </button>
                  </FormGroup>
                </form>


                <table className="table col-12 table-responsive-sm bg-white " >
                  <thead className="table table-striped">
                    <tr>
                      <th className="text-center ">URL larga</th>
                      <th className="text-center ">URL corta</th>
                    </tr>
                  </thead>
                  <tbody className="">
                    {links.map(link => {
                      let url_local = window.location;
                      url_local += link.targetLink
                      return (
                        <tr key={link.id}>
                          <td className="text-center"><a href={link.sourceLink}>{link.sourceLink}</a></td>
                          <td className="text-center"><a href={url_local} target="blank">{url_local}</a></td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
                <div className="row">
                <div className="col-12 p-0">
                  <br></br>
                  <div className="home-carousel">
                    <GlideComponent settings={slideSettings}>
                      {
                        slideItems.map((f, index) => (
                          <div key={`slide_${index}`} className="card">
                            <div className="card-body text-center">
                              <div>
                                <i className={f.icon + ' large-icon'}></i>
                                <h5 className="mb-3 font-weight-semibold">{f.title}</h5>
                              </div>
                              <div>
                                <p className="detail-text">{f.detail}</p>
                              </div>
                            </div>
                          </div>
                        ))
                      }
                    </GlideComponent>
                  </div>
                </div>
              </div>
              </div>




         {/*      <div className="row">
                <a className="btn btn-circle btn-outline-semi-light hero-circle-button"
                  href="#scroll" onClick={(event) => scrollTo(event, 'copyright')}>
                  <i className="simple-icon-arrow-down"></i>
                </a>
              </div> */}
            </div>
          </div>

          <div className="section">
            <div className="container" id="nosotros">
              <div className="row">
                <div className="col-12 offset-0 col-lg-8 offset-lg-2 text-center">
                   {/*  <h2>El acortador de enlaces que necesitas</h2>
                <p>
                    Ahora podrás tener el control de los enlaces que compartas
                  </p> */}
                </div>
              </div>
     
              <br></br>
              <br></br>

              <div className="row">
                <div className="col-12 offset-0 col-lg-8 offset-lg-2 text-center">
                  <NavLink className="btn btn-light btn-xl mr-2 mb-2" to='/user/register'>
                    REGÍSTRATE AQUÍ! <i className="simple-icon-arrow-right"></i>
                  </NavLink>
                </div>
              </div>
            </div>
          </div>




          <div className="section background background-no-bottom mb-0 pb-0" >
            <div className="container">
              <div className="row">
                <div className="col-12 offset-0 col-lg-8 offset-lg-2 text-center" id="aliados">
                  <h1>Nuestros Aliados</h1>                
                
                </div>
                <div className="col-12 offset-0 col-lg-6 offset-lg-3 newsletter-input-container">
            
                
                  <a href="https://www.auconsis.com.ec" target="_blank">
                    <img className="" alt="" width="30%" 
                      src="/assets/logos/logo-auconsis.png" />
                  </a>

                 <a className="" href="https://habitanto.com" target="_blank">
                    <img className="" alt="" width="25%"
                      src="/assets/logos/logo-habitanto.png" />
                  </a> 
                  <a className="" href="http://neuralgp.com" target="_blank">
                    <img className="" alt="" width="30%"
                      src="/assets/logos/logo-neural.png" />
                  </a> 
              
                </div>
            
              </div>
    
            </div>
          </div>

          <div className="section footer mb-0" ref={refSectionFooter}>
            <div className="container">
              <div className="row footer-row">
                <div className="col-12 text-right">
                  <a className="btn btn-circle btn-outline-semi-light footer-circle-button c-pointer"
                    href="#scroll" onClick={(event) => scrollTo(event, 'home')}><i className="simple-icon-arrow-up"></i></a>
                </div>
                <div className="col-12 text-center footer-content">
                  <a className="c-pointer" href="#scroll" onClick={(event) => scrollTo(event, 'home')}>
                    <img className="footer-logo" alt="footer logo"
                      src="/assets/logos/logo_gotly.svg" />
                  </a>
                  <h2 className="text-white">El acortador de enlaces que necesitas</h2>
                </div>
              </div>
            </div>
            <div className="container copyright pt-5 pb-5">
              <div className="row">
                <div className="col-12"></div>
                <div className="col-12 text-center">
                  <p className="mb-0">© 2021 Todos los derechos reservados</p>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div >
    </div >
  )
};
export default Home;
